@import "./variables&functions";
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700&display=swap");

html {
  background-color: $backgroundColor;
  color: $mainTextColor;
  font-family: $font-stack;
}

* {
  box-sizing: border-box;
}

nav {
  @include flexCustom(space-between, center, row);

  .nav__right {
    @include flexCustom(space-around, center, row);
    width: 200px;
    font-size: 22px;
    margin-right: 5rem;
  }

  .navigation__links {
    @include flexCustom(left, center, row);
    margin-left: 2rem;

    .navicons {
    }

    ul {
      @include flexCenter(row);
      font-size: clamp(14px, 1vw, 18px);

      li {
        list-style: none;
        margin-right: 1.5rem;
      }
    }
  }
  img {
    width: clamp(100px, 5vw, 150px);
  
  }
}

.card {
  position: relative;
  margin-right: 1vw;
  & > :first-child {
    width: 17vw;
    border-radius: 8px;
   
  }

  &:hover > .card__detailed {
    @include visible;
    height: 260%;
    width: 135%;
  }

  .card__detailed {
    @include absolutecenter;
    @include invisible;
    @include flexCustom(top, center, column);

    height: 100%;
    width: 100%;
    transition: all 300ms;
    background-color: $card;
    border-radius: 8px;

    .card__detailed--bottom {
      width: 100%;

      .card__detailed--icons {
        font-size: 2em;
        width: 100%;
        display: flex;
        padding: 10px 20px;
        

        & > div:first-child {
          width: 60%;
          display: flex;
          justify-content: space-around;

          & > *{
            cursor: pointer;
          }


        }
        & > div:nth-child(2) {
          width: 40%;
          display: flex;
          justify-content: end;

          & > *{
            cursor: pointer;
          }

        }
      }

      .card__detailed--ra-name {
        @include flexCustom(left, top, row);
        width: 100%;
        padding: 0 20px;
        text-align: left;
        font-size: 1.1em;
       

        & > p:nth-child(1) {
          color: rgb(229, 9, 20);
          border-top: solid 2px white;
          border-bottom: solid 2px white;
        }

        & > div:nth-child(2) {
          margin-left: 30px;
          @include flexCenter(row);
          flex-wrap: wrap;



          & > p:nth-child(2){
            margin: 0 0.5em;
          }

        }

        .star-icon{
          margin-left: 0.4em;
          color: $star;
          padding-top: 0.2em
        }
      }

      ul{
        @include flexCustom(left, center, row);

          li{
           margin-right: 2rem;
            list-style:circle;
            font-size: 0.7vw;
          }
      }

    }

    .card__detailed--top {
      .card__detailed--image {
        width: 100%;
      }
    }
  }


}

.trending {
  font-size: 1vw;
  margin: 0px 3vw;
  width: 100vw;
  height: fit-content;

  .tending__cards {
    display: flex;
  }
}

.wrapper {
  padding: 10vw 0px;
  overflow-x: hidden;
  overflow-y: hidden;
}

.modal__wrapper{
@include absolutefill;
position: fixed;
background-color: rgba(0, 0, 0, 0.2);;
z-index: 99;

.modal{
@include absolutecenter;
width: 50vw;
height: 80vh;
background-color: aqua;
position: fixed;
z-index: 99;
}
}
